import React, { Component } from 'react';
import land from '../assets/landtoken.png';
import corn from '../assets/corntoken.png';

class Farm extends Component {
  constructor(props) {
    super(props);

    this.state = {
      allocateLand: 0,
      compostCornAmount: 0
    };

    this.handleAllocateLand = this.handleAllocateLand.bind(this);
    this.maxAllocateLand = this.maxAllocateLand.bind(this);
    this.allocateLand = this.allocateLand.bind(this);
    this.releaseLand = this.releaseLand.bind(this);
    this.harvestCorn = this.harvestCorn.bind(this);
    this.handleCompostCornAmount = this.handleCompostCornAmount.bind(this);
    this.maxCompostCornAmount = this.maxCompostCornAmount.bind(this);
    this.compostCorn = this.compostCorn.bind(this);
  }

  // Allocate Land
  handleAllocateLand(e) {
    this.setState({ allocateLand: e.target.value });
  }

  maxAllocateLand() {
    this.setState({ allocateLand: this.props.contracts.landBalanceTrue });
  }

  allocateLand() {
    this.props.ethereumservice.allocateLand(this.state.allocateLand);
  }

  // Release Land
  releaseLand() {
    this.props.ethereumservice.releaseLand();
  }

  // Harvest Corn
  harvestCorn() {
    this.props.ethereumservice.harvestCorn();
  }

  // Compost Corn
  handleCompostCornAmount(e) {
    this.setState({ compostCornAmount: e.target.value });
  }

  maxCompostCornAmount() {
    this.setState({ compostCornAmount: this.props.contracts.cornBalanceTrue });
  }

  compostCorn() {
    this.props.ethereumservice.compostCorn(this.state.compostCornAmount);
  }

  render() {
    return (
      <section id="farm" className="is-vcentered hero is-fullheight">
        {!this.props.contracts.isLoaded && (
          <div className="hero-body mt-6 container">
            <div className="tile is-parent">
              <article className="tile is-child box has-background-light">
                <h2 className="title is-empty">Connect Wallet</h2>
              </article>
            </div>
          </div>
        )}
        {this.props.contracts.isLoaded && (
          <div className="hero-body mt-6 container">
            <div className="tile is-ancestor">
              <div className="tile is-vertical is-12">
                <div className="tile">
                  <div className="tile is-parent">
                    <article className="tile is-child box has-background-light">
                      <div className="title-control">
                        <img className="token" src={land} width="35" />
                        <h2 className="title">Manage Farm</h2>
                        {!this.props.contracts.isAuthorized && (
                          <button className="button is-rounded is-warning is-small" onClick={() => this.props.ethereumservice.approveContract()}>Grant planning permission</button>
                        )}
                      </div>
                      <div className="content">{this.renderManageFarm()}</div>
                    </article>
                  </div>
                  <div className="tile is-parent">
                    <article className="tile is-child box has-background-light">
                      <div className="title-control">
                        <img className="token" src={corn} width="35" />
                        <h2 className="title">Tend Your Crops</h2>
                      </div>
                      <div className="content">{this.renderTendCrops()}</div>
                    </article>
                  </div>
                </div>
                <div className="tile">
                  <div className="tile is-parent">
                    <article className="tile is-child box has-background-light">
                      <h2 className="title">Your Farmland Statistics</h2>
                      <div className="content">{this.renderFarmStats()}</div>
                    </article>
                  </div>
                  <div className="tile is-parent">
                    <article className="tile is-child box has-background-light">
                      <h2 className="title">Global Farmland Statistics</h2>
                      <div className="content">{this.renderGlobalStats()}</div>
                    </article>
                  </div>
                </div>
              </div>
            </div>
          </div>
        )}

      </section >
    );
  }

  renderManageFarm() {
    return (
      <div>
        <div className="info-control"> {/* UNALLOCATED */}
          <h2 className="info">Unallocated land :</h2>
          <h2 className="value">{this.props.contracts.landBalance} LAND</h2>
          <div className="info-form info-action">
            <a className="max" onClick={this.maxAllocateLand}>Max</a>
            <div className="field has-addons">
              <div className="control input-control">  
                <input className="input is-rounded is-primary is-small" type="text" placeholder="LAND Amount" value={this.state.allocateLand} onChange={this.handleAllocateLand} disabled={!this.props.contracts.isAuthorized}/>
              </div>
              <div className="control">
                <button className="button is-rounded is-primary is-small" onClick={this.allocateLand} disabled={!this.props.contracts.isAuthorized}> Allocate Land </button>
              </div>
            </div>
          </div>
        </div>

        <div className="info-control"> {/* FARMLAND */}
          <h2 className="info">Farm land :</h2>
          <h2 className="value">{this.props.contracts.landInFarm} LAND</h2>
          <div className="info-action">
            <button className="button is-rounded is-primary is-small" onClick={this.releaseLand} disabled={!this.props.contracts.isAuthorized}>Release Land</button>
          </div>
        </div>
      </div>
    )
  }

  renderTendCrops() {
    return (
      <div>
        <div className="info-control">
          <h2 className="info">Corn growing :</h2>
          <h2 className="value">{this.props.contracts.cornToHarvest} CORN</h2>
          <div className="info-action">
            <button className="button is-rounded is-primary is-small" onClick={this.harvestCorn} disabled={!this.props.contracts.isAuthorized}>Harvest Corn </button>
          </div>
        </div>

        <div className="info-control"> {/* AVAILABLE */}
          <h2 className="info">Available corn :</h2>
          <h2 className="value">{this.props.contracts.cornBalance} CORN</h2>
          <div className="info-form info-action">
            <a className="max" onClick={this.maxCompostCornAmount}>Max</a>
            <div className="field has-addons ">
              <div className="control input-control">
                <input className="input is-rounded is-primary is-small" type="text" placeholder="CORN Amount" value={this.state.compostCornAmount} onChange={this.handleCompostCornAmount} disabled={!this.props.contracts.isAuthorized}/>
              </div>
              <div className="control">
                <button className="button is-rounded is-primary is-small" onClick={this.compostCorn} disabled={!this.props.contracts.isAuthorized}>Compost Corn</button>
              </div>
            </div>
          </div>
        </div>
      </div>
    )
  }

  renderFarmStats() {
    return (
      <div>
        <div className="info-control">
          <h2 className="info">Growing season countdown : </h2>
          <h2 className="value">{this.props.contracts.growingCycleCountDown}</h2>
        </div>

        <div className="info-control">
          <h2 className="info">Farm maturity boost : </h2>
          <h2 className="value">x {this.props.contracts.farmMaturityBoost}</h2>
        </div>

        <div className="info-control">
          <h2 className="info">Composting boost : </h2>
          <h2 className="value">x {this.props.contracts.farmCompostBoost}</h2>
        </div>

        <div className="info-control">
          <h2 className="info">Corn composted : </h2>
          <h2 className="value">{this.props.contracts.cornComposted}</h2>

          {this.props.contracts.burnRequiredTo10x && (
            <h2 className="sub">({this.props.contracts.burnRequiredTo10x.toFixed(2)} more to reach x 10)</h2>
          )}
        </div>
      </div>
    )
  }

  renderGlobalStats() {
    return (
      <div>
        <div className="info-control">
          <h2 className="info">Corn composted : </h2>
          <h2 className="value">{this.props.contracts.globalComposted}</h2>
        </div>

        <div className="info-control">
          <h2 className="info">Land in Farms : </h2>
          <h2 className="value">{this.props.contracts.globalLandInFarm}</h2>
        </div>
      </div>
    )
  }

}

export default Farm;
